import { createRouter, createMemoryHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const router = createRouter({
  history: createMemoryHistory(),
  routes: [
    {
      path: '/',
      name: 'home',
      component: import('../views/UpDownCastView.vue')
    },
    {
      path: '/up-down-cast',
      name: 'up-down-cast',
      component: () => HomeView 
    },
    {
      path: '/abstraction',
      name: 'abstraction',
      component: () => import('../views/AbstractionView.vue')
    },
    {
      path: '/method-completion',
      name: 'method-completion',
      component: () => import('../views/MethodCompletionView.vue')
    },
    {
      path: '/method-completion-execution',
      name: 'method-completion-execution',
      component: () => import('../views/MethodCompletionExecutionView.vue')
    },
    {
      path: '/congratulations',
      name: 'congratulations',
      component: () => import('../views/CongratulationsPage.vue')
    },
    {
      path: '/request-vehicle-view',
      name: 'request-vehicle-view',
      component: () => import('../views/requestVehicleView.vue')
    },
    {
      path: '/locate-reservation-view',
      name: 'locate-reservation-view',
      component: () => import('../views/locateReservationView.vue')
    },
    {
      path: '/sign-contract-view',
      name: 'sign-contract-view',
      component: () => import('../views/SignContractView.vue')
    },
    {
      path: '/release-contract-view',
      name: 'release-contract-view',
      component: () => import('../views/releaseContractView.vue')
    },
    {
      path: '/update-key-status-view',
      name: 'update-key-status-view',
      component: () => import('../views/UpdateKeyStatusView.vue')
    }
  ]
})

export default router
